import Vue from 'vue';

/** 
    :
    class  [string][]    css 
    range  [array][]     window scroll ,  12
*/
export const ScrollChange = {
  bind(el, binding, vnode, oldvode) {
    let length = binding.value.range.length,
        isPercentError = false,
        isPercent = false,
        checkRangePercent = (value) => {
          let re = value.toString();
          return (re.split('%').length > 1);
        };

    //  class 
    if (!binding.value.class) {
      console.log('Directive error: "ScrollChange" must setting class');
      return false;
    }
    //  range 
    if (length === 0 || length > 2 || !binding.value.range) {
      console.log('Directive error: "ScrollChange" the key name:range, array length must be 1 or 2');
      return false;
    }

    //  range  %
    binding.value.range.forEach((item, i) => {
      let value = item.toString();
      //   %
      if (checkRangePercent(value)) {
        isPercent = true;
        // range  % 
        if (!checkRangePercent(value)) {
          isPercentError = true;
        }
      }
    });

    //  %
    if (isPercentError) {
      console.log('Directive error: The range array has a setting of "%", so all must be set to "%"');
      return false;
    }

    //  scroll tag ( unbind )
    el.ScrollFn = () => {
      let windowH, bodyH, newRange = [];
      
      // ,  % 
      if (isPercent) {
        windowH = window.innerHeight;
        bodyH = document.body.clientHeight;
        //  %  px
        binding.value.range.forEach((item, i) => {
          let value = Math.floor((bodyH - windowH) * parseInt(item.split('%')[0]) / 100);
          newRange.push(value);
        });
      } else {
        newRange = binding.value.range;
      }

      // 
      if (length === 1) {
        //: ie window.scrollY,  window.pageYOffset 
        if (window.pageYOffset >= newRange[0]) {
          el.classList.add(binding.value.class);
        } else {
          el.classList.remove(binding.value.class);
        }
      }
      // 
      if (length === 2) {
        if (window.pageYOffset >= newRange[0] && window.pageYOffset <= newRange[1]) {
          el.classList.add(binding.value.class);
        } else {
          el.classList.remove(binding.value.class);
        }
      }
    };
    el.ScrollFn(); // 
    window.addEventListener('scroll', el.ScrollFn, false); // 
  },
  unbind (el) {
    window.removeEventListener('scroll', el.ScrollFn); // 
  } 
}
 
//  AnnoyingBackground  Vue 
Vue.directive('scroll-change', ScrollChange)


