<template>
<div class="datapicker_area" >
  <input type="text" 
    :value="result"
    :placeholder="placeholder"
    :disabled="disabled"
    @focus="show_fn($event)"
  >
  <div :class="{'datapicker_box': true, 'right': isRight}" v-show="isShow">

    <div class="datapicker_top_box">
      <!--  -->
      <div class="prev_btn" @click="prevMonth_fn()">
        <i class="fa fa-chevron-left" aria-hidden="true"></i>
      </div>
      <span>
        <i v-text="searchYear"></i> - 
        <i v-text="(searchMonth+1)"></i> 
      </span>
      <!--  -->
      <div class="next_btn" @click="nextMonth_fn()">
        <i class="fa fa-chevron-right" aria-hidden="true"></i>
      </div>
    </div>

    <ul class="thead">
      <li v-for="item in theadAry"><span>{{item.weekTxt}}</span></li>
    </ul>

    <!--  -->
    <ul class="tbody" v-if="!useSlot">
      <li v-for="item in datesAry"
        :class="{
          active: (item.yearTxt === choiseYear &&
                  item.dayTxt === choiseDay &&
                  item.monthTxt === choiseMonth),
          disable: checkDtaeIsDisable_fn(item)
        }"
        @click="cliickDate_fn(item)">
        <span>{{item.dayTxt}}</span>
      </li>
    </ul>
      
    <!--  html ,  li  ul  -->
    <ul class="tbody" v-if="useSlot">
      <slot name="dateSlot" 
        :dateReturn="datesAry" 
        :clickDate="cliickDate_fn" 
        :checkDate="checkDtaeIsDisable_fn">
      </slot>
    </ul>

  </div>
  <div class="datapicker_maskbg" v-show="isShow" v-if="!keepshowPicker" 
    @click="hide_fn()"></div>

</div>
</template>

<script>
// [v] 
const datepickerLg = {
  "tw": [
    { weekTxt: '' },
    { weekTxt: '' },
    { weekTxt: '' },
    { weekTxt: '' },
    { weekTxt: '' },
    { weekTxt: '' },
    { weekTxt: '' }
  ],
  "ch": [
    { weekTxt: '' },
    { weekTxt: '' },
    { weekTxt: '' },
    { weekTxt: '' },
    { weekTxt: '' },
    { weekTxt: '' },
    { weekTxt: '' }
  ],
  "en": [
    { weekTxt: 'Su' },
    { weekTxt: 'Mo' },
    { weekTxt: 'Tu' },
    { weekTxt: 'We' },
    { weekTxt: 'Th' },
    { weekTxt: 'Fr' },
    { weekTxt: 'Sa' }
  ]
};

/**  
  result          []   
  disabled        []  input 
  placeholder     []  placeholder
  mindate         []  
  maxdate         []  
  callback        []   callback
  lang            []   tw, ch, en , 
  keepshowPicker  []  
  useSlot         []   slot  li  render
  
  : 
      : 2017-04-06
      inputmask
      ,
     ()
   language  ()
*/
export default {
  props: {
    result: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ''
    },
    mindate: {
      type: String,
      default: ''
    },
    maxdate: {
      type: String,
      default: ''
    },
    callback: {
      type: Function,
      default: null
    },
    lang: {
      type: String,
      default: 'tw'
    }, 
    keepshowPicker: {
      type: Boolean,
      default: false
    },
    useSlot: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      // 
      isShow: false,
      // 
      isRight: false,
      // 
      searchYear: 2018,
      searchMonth: 5,
      searchDay: 22,
      // 
      choiseYear: null,
      choiseMonth: null,
      choiseDay: null,
      // 
      datesAry: [],
      //  (tw)
      theadAry: datepickerLg.tw,
    }
  },
  methods: {
    // [F]  day ()
    getAll_Reservation_fn (get_y, get_m) {
      this.datesAry = []; // 

      let searchDate,     // 
          firstWeek,      // 
          allDay,         // 
          vacancyPrev,    // 
          vacancyNext,    // 
          i = null;

      searchDate = new Date(get_y, get_m, 1);
      firstWeek = searchDate.getDay();

      // setDate(0)
      searchDate.setMonth(searchDate.getMonth() + 1);
      searchDate.setDate(0);
      allDay = searchDate.getDate();

      // 
      vacancyPrev = (7 - (7 - firstWeek));  // 7 - 
      vacancyNext = (7 - ((allDay + vacancyPrev) % 7)); // 7 -  + vacancyPrev 7

      //  ng-repeat 
      for (i = 0; i < allDay + vacancyPrev + vacancyNext; i++) {
        let oneDayObj = {
            dayTxt: '',      // td
            monthTxt: '',    // td
            yearTxt: '',     // td
            fullValue: null  // td(0)
        },
        tempDayTxt,          // 
        tempMonthTxt;        // 

        // ()
        if ((i > vacancyPrev - 1) && (i < vacancyPrev + allDay)) {
          tempDayTxt = (i - vacancyPrev) + 1;
          tempMonthTxt = this.searchMonth + 1; // : -1, 87+1

          // 0
          if (tempDayTxt <= 9) {
            tempDayTxt = '0' + tempDayTxt;
          }

          // 0
          if (tempMonthTxt <= 9) {
            tempMonthTxt = '0' + tempMonthTxt;
          }

          oneDayObj.dayTxt = (i - vacancyPrev) + 1;  // tempDayTxt0
          oneDayObj.monthTxt = this.searchMonth;  // tempMonthTxt0
          oneDayObj.yearTxt = this.searchYear;
          oneDayObj.fullValue = this.searchYear + '-' + tempMonthTxt + '-' + tempDayTxt;  // 2017-04-16
        }

        this.datesAry.push(oneDayObj);
      }
      // 
      //console.log(this.datesAry);
      //console.log(searchDate);
      //console.log(": " + firstWeek);
      //var startDAY = get_y + "-" + (parseInt(get_m)+1) + "-1";  // 
      //var endDAY = get_y + "-" + (parseInt(get_m)+1) + "-" + (searchDate.getDate());  // 
      //console.log(': ' + vacancyPrev);
      //console.log(': ' + vacancyNext);
      //console.log(': ' + startDAY);
      //console.log(': ' + endDAY);
      //console.log(': ' + allDay);
    },
    // [E] 
    prevMonth_fn () {
      if (this.searchMonth === 0) {
        //
        this.searchYear = this.searchYear - 1;
        this.searchMonth = 12;
      }
      this.searchMonth = this.searchMonth - 1;
      this.getAll_Reservation_fn(this.searchYear, this.searchMonth);
    },
    // [E] 
    nextMonth_fn () {
      if (this.searchMonth === 11) {
        // 
        this.searchYear = this.searchYear + 1;
        this.searchMonth = -1;
      }
      this.searchMonth = this.searchMonth + 1;
      this.getAll_Reservation_fn(this.searchYear, this.searchMonth);
    },
    // [E] 
    show_fn (event) {
      this.isShow = true;
      //  disable:none  clientWidth 
      setTimeout(() => {
        // 
        let parents = event.target.parentNode.childNodes,
            $datepickerBox = parents[2],
            maxWidth = window.innerWidth,
            left = $datepickerBox.getBoundingClientRect().x,
            width = $datepickerBox.getBoundingClientRect().width;
        
        this.isRight = ((left + width) >= maxWidth)? true : false;
        this.inputKeyInCheck_fn(); // 
      }, 0);
    },
    // [E] 
    hide_fn () {
      if (!this.keepshowPicker) {
        this.isShow = false;
        this.isRight = false;
        this.inputKeyInCheck_fn(); // key in
      }
    },
    // [F] 
    stringToDate_fn (_txt) {
      let re = {
              year: null,
              month: null,
              day: null
          },
          d = new Date(_txt);
      if (_txt !== null) {
        re = {
          year: d.getFullYear(),
          month: d.getMonth(),  // 87
          day: d.getDate()
        };
      }
      return re;
    },
    // [F]  key in 
    inputKeyInCheck_fn () {
      let getVal = this.stringToDate_fn(this.result), // 
          resetChoise_fn = (self) => {
            //
            self.choiseYear = null;
            self.choiseMonth = null;
            self.choiseDay = null;
          },
          validate = true;

      // 
      if (this.result === '' || this.result === null) {
        validate = false;
      }

      // 
      if (validate) {
        if (this.result.length < 10 || this.result.length > 10) {
          validate = false;
        }
      }

      // 
      if (isNaN(getVal.year) || isNaN(getVal.month) || isNaN(getVal.day)) {
        validate = false;
      }

      // 
      if (validate) {
        let check = this.checkDtaeIsDisable_fn({ fullValue: this.result });
        if (check) {
          validate = false;
        }
      }
      
      //  model 
      if (validate) {
        this.choiseYear = getVal.year;
        this.choiseMonth = getVal.month;
        this.choiseDay = getVal.day;
      } else {
        resetChoise_fn(this);
        // 
        this.$emit('update:result', null); //  .sync 
      }

      // callbackfunction
      if (typeof this.callback === 'function') {
        this.callback();
      }
    },
    // [E] click
    cliickDate_fn (item) {
      if (item.fullValue) {
        // 
        if (this.result === item.fullValue) {
          return false;
        }

        // 
        if (this.checkDtaeIsDisable_fn(item)) {
          return false;
        }

        // 
        this.choiseYear = item.yearTxt;
        this.choiseMonth = item.monthTxt;
        this.choiseDay = item.dayTxt;

        // 
        this.$emit('update:result', item.fullValue); //  .sync 

        //  callback  function
        if (typeof this.callback === 'function') {
          this.callback();
        }
      }
    },
    // [F] 
    stringToDateTime_fn (_txt) {
      let re = new Date(_txt);
      return re.getTime();
    },
    // [F] 
    checkDtaeIsDisable_fn (item) {
      let isDisable = false,  // 
          checkDate,  //  min, max
          itemDate = this.stringToDateTime_fn(item.fullValue);   //  item 

      //  
      if (this.mindate) {
        // 
        checkDate = this.stringToDateTime_fn(this.mindate);
        if (itemDate < checkDate) {
          isDisable = true;
        }
      }
      //  
      if (this.maxdate) {
        // 
        checkDate = this.stringToDateTime_fn(this.maxdate);
        if (itemDate > checkDate) {
          isDisable = true;
        }
      }
      return isDisable;
    }
  },
  mounted () {
    let today, creatData;

    // 
    if (this.lang) {
      if (this.lang !== 'tw' && this.lang !== 'ch' && this.lang !== 'en') {
        console.log(',tw');
      }
    }
    this.theadAry = datepickerLg[this.lang];

    //  placeholder
    if (this.placeholder) {
      if (typeof this.placeholder !== 'string') {
        console.log('placeholder, ');
      }
    }

    // 
    this.isShow = (this.keepshowPicker)? true : false;

    //  result
    if (!this.result) {
      this.result = ''; // 
      // , 
      today = new Date();
      // 
      this.searchYear = today.getFullYear();
      this.searchMonth = today.getMonth();
      this.searchDay = today.getDate();
      this.getAll_Reservation_fn(this.searchYear, this.searchMonth);
    } else {
      //  , , , (0)
      /* var ary = this.result.split('-'),
        creatData = new Date(ary[0], ary[1], ary[2]); */
      creatData = new Date(this.result);
      // 
      this.searchYear = creatData.getFullYear();
      //this.searchMonth = creatData.getMonth() - 1;  //: -1, 87
      this.searchMonth = creatData.getMonth();
      this.searchDay = creatData.getDate();
      this.getAll_Reservation_fn(this.searchYear, this.searchMonth);

      // 
      this.choiseYear = this.searchYear;
      this.choiseMonth = this.searchMonth;
      this.choiseDay = this.searchDay;
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '../../scss/style.main.scss'; //TODO... scss...
  @import './style.scss';
</style>


