import axios from 'axios'

const ajaxDefine = {
  data () {
    return {
      ajaxCount: 0
    }
  },
  methods: {
    calcAjaxCount (p) {
      this.$root.ajaxCount += p; //  this , $root
      //console.log(this.$root.ajaxCount);
    },
    ajaxCall (method, path, parm) {
      //  ajax   
      this.calcAjaxCount(1); //  ajx +1
      return axios({method:method, url: path, params: parm}).then((result) => {
        this.calcAjaxCount(-1);
        return result.data;
      }).catch((error) => {
        this.calcAjaxCount(-1);
        console.log(error);
      });
    },
    rootHttpPost (path, parm) {
      return this.ajaxCall('post', path, parm);
    },
    rootHttpGet (path, parm) {
      return this.ajaxCall('get', path, parm);
    },
    rootHttpAll (ary) {
      return axios.all(ary).then((result) => {
        return result;
      });
    }
  }
}

export default ajaxDefine;


